import Landing from "./Landing.mdx";
import page_config from "../page_config";

export default page_config(Landing, null, {
  route: "/",
  no_header: true,
  on_page_load,
});

function on_page_load() {
  if (window.location.hash === "#lsos-commons") {
    remove_hash();
    window.location.pathname = "/commons";
    return;
  }
  if (window.location.hash === "#lsos-growth") {
    remove_hash();
    window.location.pathname = "/growth";
    return;
  }
}

function remove_hash() {
  // `window.location.hash = '';` doesn't remove leading `#` sign.
  // See https://stackoverflow.com/questions/1397329/how-to-remove-the-hash-from-window-location-url-with-javascript-without-page-r
  window.history.pushState(
    "",
    document.title,
    window.location.pathname + window.location.search
  );
}
